@font-face {
  font-family: 'PuradakGentleGothicR';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_11-01@1.1/PuradakGentleGothicR.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}



* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'PuradakGentleGothicR';
}
li {
  list-style: none;
}

.list {
  max-width: 640px;
  width: calc(100% - 20px);
  margin: 100px auto;
  background-color: #fff;
  border: 2px solid #aaa;
  padding: 30px 130px;
  text-align: center;
}


.list h2 {
  font-size: 40px;
  margin-bottom: 50px;
  
}

.list input {
  width: 300px;
  height: 40px;
  padding: 0 10px;
  border-radius: 50px;
  border: 2px solid #ddd;
  margin-right: 10px;
  margin-bottom: 20px;

}

.list button {
  width: 40px;
  height: 40px;
  color: #fff;
  background-color:steelblue;
  border: none;
  border-radius: 50px;
  cursor: pointer;
}

.list ul li{
  position: relative;
  text-align: start;
  border-bottom: 1px dashed #ddd;
  padding: 10px;
}

.list ul li span::before {
  content: '💣';
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  z-index: 99999;
}

/* nth-child(2n) =>2배수
nth-child(odd) => 홀수
nth-child(even) => 짝수 */